/* 
#
#24232C
#817D92
#E6E5EA
#18171F
#A4FFAF
#F64A4A
#FB7C58
#F8CD65


*/

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}

body {
  font-family: "JetBrains Mono", monospace;
  font-weight: 700;
  height: 100vh;
  line-height: normal;
  font-style: normal;
}

img {
  max-width: 100%;
}

main {
  width: 54rem;
  max-width: 54rem;
}

.container {
  background-color: #18171f;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.main-heading {
  font-size: 2.4rem;
  margin-bottom: 3.1rem;
  color: #817d92;
}

.box {
  background-color: #24232c;
  padding: 2.4rem 3.4rem 3.2rem 3.2rem;
}

.heading {
  font-size: 3.2rem;
}

.generated-password {
  width: 100%;
  height: 8rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 3.2rem;
  margin-bottom: 2.4rem;
  background-color: #24232c;
}

.copied-box {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1.6rem;
}

.copied {
  visibility: visible;
  font-size: 1.8rem;
  color: #a4ffaf;
  text-transform: uppercase;
}

.password {
  font-size: 3.2rem;
  color: #e6e5ea;
}

.not-real-password {
  color: #e6e5ea;
  font-size: 3.2rem;
  opacity: 0.25;
}

.length-section {
  background-color: #24232c;
  margin-bottom: 4.2rem;
}

.length-desc {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 2.5rem;
}

.length-label {
  font-size: 1.8rem;
  color: #e6e5ea;
}

.length-number {
  font-size: 3.2rem;
  color: #a4ffaf;
}

.length-input {
  width: 100%;
  height: 0.8rem;
  color: #a4ffaf;
  background: #a4ffaf;
  accent-color: #a4ffaf;
}

.length-input::-webkit-slider-thumb {
  -webkit-appearance: none;
  height: 2.8rem;
  width: 2.8rem;
  border-radius: 50%;
  background: #18171f;
  cursor: pointer;
}

.include-section {
  background-color: #24232c;
  display: flex;
  align-items: center;
  gap: 2.4rem;
}

/* The container */
.include-box {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  padding-left: 4.5rem;
  margin-bottom: 1.9rem;
  cursor: pointer;
  font-size: 22px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

/* Hide the browser's default checkbox */
.include-box input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}

/* Create a custom checkbox */
.checkmark {
  position: absolute;
  top: 0;
  left: 0;
  height: 2rem;
  width: 2rem;
  background-color: #18171f;
  border: 2px solid #e6e5ea;
}

/* On mouse-over, add a grey background color */
.include-box:hover input ~ .checkmark {
  background-color: #18171f;
  border: 2px solid #a4ffaf;
}

/* When the checkbox is checked, add a blue background */
.include-box input:checked ~ .checkmark {
  background-color: #a4ffaf;
  border: none;
}

/* Create the checkmark/indicator (hidden when not checked) */
.checkmark:after {
  content: "";
  position: absolute;
  display: none;
}

/* Show the checkmark when checked */
.include-box input:checked ~ .checkmark:after {
  display: block;
}

/* Style the checkmark/indicator */
.include-box .checkmark:after {
  left: 6px;
  top: 2px;
  width: 5px;
  height: 10px;
  border: solid #18171f;
  border-width: 0 3px 3px 0;
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
}

.include-label {
  font-size: 1.8rem;
  color: #e6e5ea;
}

.strength-section {
  margin-top: 1.2rem;

  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #18171f;
  height: 7.2rem;
  padding: 0 3.2rem;
  margin-bottom: 3.2rem;
}

.strength-heading {
  font-size: 18px;
  font-weight: 700;
  color: #817d92;
  text-transform: uppercase;
}

.strength-bars {
  display: flex;
  gap: 0.8rem;
}

.too-weak {
  background-color: #f64a4a;
}

.weak {
  background-color: #fb7c58;
}

.medium {
  background-color: #f8cd65;
}

.strong {
  background-color: #a4ffaf;
}

.strength-bar {
  width: 1rem;
  height: 2.8rem;
  border: 2px solid hsl(252, 11%, 91%);
}

.strength-text {
  font-size: 24px;
  color: #e6e5ea;
  margin-right: 1.5rem;
  text-transform: uppercase;
}

.generate-button {
  height: 6.5rem;
  width: 100%;
  background-color: #24232c;
  font-size: 1.8rem;
  color: #a4ffaf;
  border: 2px solid #a4ffaf;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 2.4rem;
  cursor: pointer;
  text-transform: uppercase;
}

.generate-button:hover {
  background-color: #a4ffaf;
  color: #24232c;
}

.generate-button:hover .arrow {
  filter: brightness(0) saturate(100%) invert(11%) sepia(8%) saturate(677%)
    hue-rotate(206deg) brightness(103%) contrast(92%);
}

.arrow {
  width: 1.1rem;
  height: 1.2rem;
}

.clipboard {
  width: 2.1rem;
  height: 2.4rem;
  cursor: pointer;
}

.clipboard:hover {
  filter: brightness(0) saturate(100%) invert(100%) sepia(45%) saturate(0%)
    hue-rotate(131deg) brightness(108%) contrast(100%);
}

.clipboard:active {
  filter: brightness(0) saturate(100%) invert(100%) sepia(45%) saturate(0%)
    hue-rotate(131deg) brightness(108%) contrast(100%);
}

/* Tablet */
@media (max-width: 1024px) and (min-width: 768px) {
  .heading {
    font-size: 2.4rem;
  }

  .length-section {
    background-color: #24232c;
    margin-bottom: 4.2rem;
  }

  .length-desc {
    margin-bottom: 2.7rem;
  }

  .include-label {
    font-size: 1.8rem;
    color: #e6e5ea;
  }

  .strength-heading {
    font-size: 18px;
    font-weight: 700;
    color: #817d92;
    text-transform: uppercase;
  }
}

/* Mobile */
@media (max-width: 667px) and (min-width: 375px) {
  main {
    width: 34rem;
    max-width: 34rem;
  }

  .main-heading {
    font-size: 1.6rem;
    margin-bottom: 1.6rem;
  }

  .box {
    background-color: #24232c;
    padding: 2.1rem 1.6rem 1.6rem 1.6rem;
  }

  .generated-password {
    height: 6.4rem;
    padding: 0 1.6rem;
    margin-bottom: 1.6rem;
  }

  .copied {
    font-size: 1.4rem;
  }

  .password {
    font-size: 2.4rem;
  }

  .not-real-password {
    font-size: 2.4rem;
  }

  .length-section {
    margin-bottom: 4.2rem;
  }

  .length-desc {
    margin-bottom: 2.3rem;
  }

  .length-label {
    font-size: 1.8rem;
    color: #e6e5ea;
  }

  .length-number {
    font-size: 2.4rem;
  }

  .include-label {
    font-size: 1.6rem;
  }

  .strength-section {
    height: 5.6rem;
    padding: 0 1.6rem;
    margin-bottom: 1.6rem;
  }

  .strength-heading {
    font-size: 1.6rem;
  }

  .strength-text {
    font-size: 1.8rem;
  }

  .generate-button {
    height: 5.6rem;
    font-size: 1.6rem;
    gap: 1.6rem;
  }
}

@media (max-width: 355px) and (min-width: 250px) {
  main {
    width: 32rem;
    max-width: 32rem;
  }
}
